'use client';

import { useState, useEffect } from 'react';
import Head from 'next/head';
import { db, auth } from '../lib/firebase';
import { doc, getDoc, setDoc, arrayUnion } from 'firebase/firestore';
import {
  onAuthStateChanged,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  User
} from 'firebase/auth';
import { useRouter } from 'next/navigation';

function getTodayDate() {
  return new Date().toISOString().split('T')[0];
}

export default function Home() {
  const [questions, setQuestions] = useState<string[]>([]);
  const [answers, setAnswers] = useState<{ [key: number]: string }>({});
  
  const [isCheckingUser, setIsCheckingUser] = useState(true);
  const [isFetchingQuestions, setIsFetchingQuestions] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  const [user, setUser] = useState<User | null>(null);
  const [groupId, setGroupId] = useState<string | null>(null);
  
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showAuthForm, setShowAuthForm] = useState(false);
  const [authError, setAuthError] = useState<string | null>(null);

  // --- NEW: State for the login-only modal ---
  const [showLoginModal, setShowLoginModal] = useState(false);
  // -------------------------------------------
  
  const router = useRouter();

  // --- 1. Listen for user auth changes ---
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        // If a user logs in, close any auth modals
        setShowLoginModal(false);
      }
    });
    return () => unsubscribe();
  }, []);

  // --- 2. Check if user has already played today ---
  useEffect(() => {
    const checkUserStatus = async () => {
      if (!user) {
        setIsCheckingUser(false);
        return;
      }
      const userRef = doc(db, 'users', user.uid);
      const userSnap = await getDoc(userRef);
      const today = getTodayDate();
      if (userSnap.exists()) {
        const userData = userSnap.data();
        if (userData.dailyGroup && userData.dailyGroup.date === today) {
          router.push(`/group/${userData.dailyGroup.groupId}`);
          return;
        }
      }
      setIsCheckingUser(false);
    };
    checkUserStatus();
  }, [user, router]);

  // --- 3. Fetch daily questions ---
  useEffect(() => {
    if (isCheckingUser) return;
    const fetchQuestions = async () => {
      try {
        setIsFetchingQuestions(true);
        const docRef = doc(db, 'config', 'daily');
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          const data = docSnap.data();
          if (data.date === getTodayDate()) {
            setQuestions(data.questions);
          } else {
            setError('New questions are being generated. Please check back in a few minutes.');
          }
        } else {
          setError('Could not find daily questions. Please check back later.');
        }
      } catch (err) {
        console.error(err);
        setError('Failed to load questions.');
      } finally {
        setIsFetchingQuestions(false);
      }
    };
    fetchQuestions();
  }, [isCheckingUser]);

  // --- 4. Handle answer selection ---
  const handleAnswer = (questionIndex: number, answer: string) => {
    setAnswers(prev => ({ ...prev, [questionIndex]: answer }));
  };

  // --- 5. Handle form submission ---
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (Object.keys(answers).length !== questions.length) {
      setError('Please answer all 5 questions.');
      return;
    }
    setError(null);
    const newGroupId = questions.map((_, index) => answers[index] === 'yes' ? '1' : '0').join('');
    setGroupId(newGroupId);
    if (user) {
      await assignUserToGroup(user, newGroupId, answers);
    } else {
      setShowAuthForm(true);
    }
  };

  // --- 6. Handle auth *after* answering ---
  const handleAuth = async (isSigningUp: boolean) => {
    if (!email || !password) {
      setAuthError('Please enter email and password.');
      return;
    }
    setAuthError(null);
    try {
      let userCredential;
      if (isSigningUp) {
        userCredential = await createUserWithEmailAndPassword(auth, email, password);
        const newUser = userCredential.user;
        const userRef = doc(db, 'users', newUser.uid);
        await setDoc(userRef, {
          email: newUser.email,
          isProfileComplete: false,
          username: newUser.email?.split('@')[0] || `user${Date.now()}`
        }, { merge: true });
      } else {
        userCredential = await signInWithEmailAndPassword(auth, email, password);
      }
      const loggedInUser = userCredential.user;
      if (groupId) {
        await assignUserToGroup(loggedInUser, groupId, answers);
      } else {
        router.refresh();
      }
    } catch (err: any) {
      console.error("Auth error:", err);
      setAuthError(err.message);
    }
  };
  
  // --- 7. NEW: Handle login *before* answering ---
  const handleLogin = async () => {
    if (!email || !password) {
      setAuthError('Please enter email and password.');
      return;
    }
    setAuthError(null);
    try {
      await signInWithEmailAndPassword(auth, email, password);
      // Success! The onAuthStateChanged listener will handle the rest.
      // It will close the modal and re-run checkUserStatus.
      setEmail('');
      setPassword('');
    } catch (err: any) {
      console.error("Login error:", err);
      setAuthError(err.message);
    }
  };
  // -----------------------------------------------

  // --- 8. MODIFIED: Add user to group AND save to user doc ---
  const assignUserToGroup = async (
    currentUser: User, 
    finalGroupId: string,
    rawAnswers: { [key: number]: string }
  ) => {
    if (!currentUser || !finalGroupId) return;
    console.log(`Assigning user ${currentUser.uid} to group ${finalGroupId}`);
    try {
      const today = getTodayDate();
      const groupRef = doc(db, `daily_groups/${today}/groups`, finalGroupId);
      await setDoc(groupRef, {
        members: arrayUnion(currentUser.uid)
      }, { merge: true });

      const userRef = doc(db, 'users', currentUser.uid);
      const answersAsArray = questions.map((_, index) => rawAnswers[index]);
      const dailyGroupData = {
        date: today,
        groupId: finalGroupId,
        answers: answersAsArray
      };
      const userSnap = await getDoc(userRef);
      const userData = userSnap.data();

      await setDoc(userRef, { 
        dailyGroup: dailyGroupData 
      }, { merge: true });
      
      setShowAuthForm(false);
      if (userData?.isProfileComplete) {
        router.push(`/group/${finalGroupId}`);
      } else {
        router.push(`/profile`);
      }
    } catch (err) {
      console.error('Failed to assign group:', err);
      setError('There was an error joining your group. Please try again.');
    }
  };

  // --- RENDER LOGIC ---
  if (isCheckingUser || isFetchingQuestions) {
    return <main className="loading-container">Loading...</main>;
  }
  
  if (error) {
    return <main className="error-container">{error}</main>;
  }
  
  // Renders the post-answer auth form
  if (!isFetchingQuestions && !error && showAuthForm) {
    return (
      <main>
        <div className="auth-container">
          <h2>You're in Group {groupId}!</h2>
          <p>Sign up or log in to see your group members.</p>
          <div className="auth-form">
            <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="Email" />
            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Password" />
            <button onClick={() => handleAuth(true)} className="btn-primary">Sign Up</button>
            <button onClick={() => handleAuth(false)} className="btn-secondary">Log In</button>
            {authError && <p className="auth-error">{authError}</p>}
          </div>
        </div>
      </main>
    );
  }

  // --- NEW: Renders the login-only modal ---
  if (showLoginModal) {
    return (
      <div className="login-modal-overlay">
        <div className="login-modal-content">
          <h2>Sign In</h2>
          <p>Sign in to your existing account.</p>
          <button className="btn-close-modal" onClick={() => setShowLoginModal(false)}>X</button>
          <div className="auth-form">
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Email"
            />
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Password"
            />
            <button onClick={handleLogin} className="btn-primary">Log In</button>
            {authError && <p className="auth-error">{authError}</p>}
          </div>
        </div>
      </div>
    );
  }
  // ----------------------------------------

  // Renders the main question form
  return (
    <main>
      <Head>
        <title>Brillianse - Daily Questions</title>
      </Head>

      {/* --- NEW: Sign In Button in Header --- */}
      {!user && !showAuthForm && (
        <div className="header-actions">
          <button className="btn-sign-in-header" onClick={() => {
            setAuthError(null); // Clear old errors
            setEmail('');       // Clear fields
            setPassword('');
            setShowLoginModal(true);
          }}>
            Sign In
          </button>
        </div>
      )}
      {/* ------------------------------------- */}

      {!isFetchingQuestions && !error && !showAuthForm && (
        <form onSubmit={handleSubmit} className="question-form">
          <h1>Today's 5 Questions</h1>
          <p>Answer to get matched with your daily group.</p>
          
          {questions.map((q, index) => (
            <div key={index} className="question-block">
              <p className="question-text">{index + 1}. {q}</p>
              <div className="button-group">
                <button
                  type="button"
                  className={`btn-answer ${answers[index] === 'yes' ? 'selected' : ''}`}
                  onClick={() => handleAnswer(index, 'yes')}
                >
                  Yes
                </button>
                <button
                  type="button"
                  className={`btn-answer ${answers[index] === 'no' ? 'selected' : ''}`}
                  onClick={() => handleAnswer(index, 'no')}
                >
                  No
                </button>
              </div>
            </div>
          ))}
          <button type="submit" className="btn-submit">
            Find My Group
          </button>
        </form>
      )}
    </main>
  );
}